
@font-face {
  font-family: "iconfont";
  src: url(data:application/vnd.ms-fontobject;base64,);
  /* IE9*/
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format("embedded-opentype"), url("data:application/x-font-woff;charset=utf-8;base64,") format("woff"), url(data:application/x-font-ttf;base64,) format("truetype"), url(data:image/svg+xml;base64,#iconfont) format("svg");
  /* iOS 4.1- */
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-back:before {
  content: "\E697";
}
.icon-close:before {
  content: "\E69A";
}
.cascade * {
  margin: 0;
  padding: 0;
}
.select {
  width: 270px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin: 30px auto;
  display: block;
  font-size: 14px;
  cursor: pointer;
}

.cascade__mask {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
}
.cascade__content {
  width: 80%;
  max-width: 350px;
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  background: #fff;
  border-left: 1px solid #efefef;
}
.cascade__header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-bottom: 1px dashed #efefef;
}
.cascade__header .icon-back {
    font-size: 22px;
    cursor: pointer;
}
.cascade__title {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-indent: -20px;
  font-weight: bold;
}
.cascade__search {
  height: 40px;
  padding: 0 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.cascade__search--input {
    border: none;
    width: 100%;
    text-align: center;
    -webkit-appearance: none;
    outline: none;
}
.cascade__search--comfirm {
    position: absolute;
    width: 60px;
    height: 40px;
    background: #495060;
    right: 0;
    line-height: 40px;
    text-align: center;
    font-size: 13px;
    color: #fff;
    cursor: pointer;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    z-index: 9;
}
.cascade__list {
  height: calc(100vh - 41px);
  overflow-y: scroll;
  padding-right: 17px;
  overflow-x: hidden;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  width: 100%;
}
.cascade__item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: calc(106%);
  padding: 12px 20px;
  border-bottom: 1px solid #efefef;
  cursor: pointer;
  -webkit-transition: all 0.15s ease;
  transition: all 0.15s ease;
  font-size: 14px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.cascade__item p {
    text-align: left;
    margin-left: -8px;
}
.cascade__item:hover {
    background: #fafafa;
}
.cascade__loading {
  width: 50px;
  margin: 20px auto;
}
.cascade__loading img {
    max-width: 100%;
}
.cascade__noResultTip, .cascade__noSearchResultTip {
  text-align: center;
  margin-top: 20px;
  font-size: 14px;
  color: #48505f;
}
.filterable .cascade__list {
  border-top: 1px solid #efefef;
  height: calc(100vh - 81px);
}
